.blognews{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}
.blogMainImag{
    max-width: 900px;
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
}
.blogMainImag img{
    width: 100%;
    height: 500px;
    object-fit: cover;
}
.someText{
    position: absolute;
    background: rgba(0,0,0,.5);
    max-width: 500px;
    width: 100%;
    padding: 50px 20px;
    border-radius: 8px;

}
.someText h1{
    color: #29658d;
    margin-top: 10px;
}
.someText p{
    color: #fff;
    margin-top: 10px;
}
.blogText{
    margin: 30px 10px;
    max-width: 900px;
    width: 100%;
    padding: 20px;

}
.blogSmall{
    max-width: 900px;
    width: 100%;
    height: 200px;
    display: flex;   
    align-items: center;
    box-shadow: 1px 1px 3px rgba(0,0,0,.3), -1px -1px 3px rgba(0,0,0,.3);
    border-radius: 8px;
    overflow: hidden;
    transition: 0.5s;
}
.blogSmall:hover{
    box-shadow: 1px 1px 5px rgba(0,0,0,.3), -1px -1px 5px rgba(0,0,0,.3);
}
.blogSmall_img {
    overflow: hidden;
    width: 30%;
    
}
.blogSmall_img img{
    max-width: 300px;
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
 
    
}
.blogSmall_text{
    width: 70%;
    height: 100%;
    padding: 10px 20px;
    
}
.blogSmall_text h3{
    color: #0b507e;
    padding: 10px 0;
}
@media(max-width: 700px){
    .blogMainImag img{
     
        height: 350px;}
}
@media(max-width: 600px){
    .blogSmall_img{
        width: 100%;
        border-top-left-radius: 0px;
        border-bottom-left-radius: 0px;
    }
    .blogSmall_img img{
        max-width: 600px;
        height: 300px;
    }
    .blogMainImag img{
        height: 300px;
    }
    .blogSmall{
         flex-direction: column;
        height: auto;
        }
    .blogSmall_text{
        width: 100%;
    }
}