.app_gallery_heading{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 4rem 2rem;
    position: relative;
}

.app_gallery_heading::after{
    content: '';
    position: absolute;
    top: 65%;
    text-align: center;
    width: 50px;
    height: 2px;
    opacity: 0.7;
    background-color: #3889bc;
}

.app_gallery_content{
    flex:1;
    display: flex ;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
    min-width: 50%;
    position: relative;
}
.app_gallery_images {
    display: flex;
    flex-direction: row;
    width: max-content;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.app_gallery_images::-webkit-scrollbar{
    display: none;
}
.gallery_image_card{
    position: relative;
    min-width: 301px;
    height: 380px;
    margin-right: 1.5rem;
}
.gallery_image_card img{
  width: 100%;
  height: 100%;
  opacity: 1;
  object-fit: cover;
  transition: 0.5s ease;
}

.app_gallery_icons{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    position: absolute;
    bottom: 5%;
}

.gallery_arrow{
    color: #3889bc;
    font-size: 2rem;
    cursor: pointer;
    background: #000;
    border-radius: 5px;
    transition: 0.5s;
}
.gallery_arrow:hover{
    color: #fff;
}
.gallery__image-icon{
    position: absolute;
    color: #fff;
    font-size: 2rem;
    opacity: 0.5;
    cursor: pointer;
    transition: 0.5s;
}
.gallery_image_card:hover img{
    opacity: 0.8;
}
.gallery_image_card:hover .gallery__image-icon{
    opacity: 1;
}
