.heading{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 4rem 2rem;
    position: relative;
}
.heading::after{
    content: '';
    position: absolute;
    top: 68%;
    text-align: center;
    width: 50px;
    height: 2px;
    opacity: 0.7;
    background-color: #0b507e;
}
