.scrollTop{
    width: 100%;
}
.scroll_btn{
   position: fixed; 
   bottom: 70px;
   right: 25px;   
   background-color: black;
   border-radius: 5px;
   font-size: 2rem;
   z-index: 10;
   cursor: pointer;
   color: #0b507e;
   transition: 0.5s ease;
}
.scroll_btn:hover{
    color: #fff;
}