.app_navbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.2rem 6rem;
  
}
.app_navbar_logo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    z-index: 100;
}
.app_navbar_logo img{
    width: 140px;
    
}
.app_navbar_links{
    
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    
}

.app_navbar_links li ul {
    position: relative;
    top: 70%;
    width: 200px;
    height: auto;
    list-style: none;
    z-index: 15;
    transition: 0.5s;
    display: none;
  }
   
  .app_navbar_links  li a {
    display: block;
    padding: 1em;
    text-decoration: none;
    background: #fff;
    
  
  }
  .app_navbar_links ul ul {
      position: absolute;
      display: none;
    left: 100%;
    top: 0;
  }

  .app_navbar_links ul li a:hover {
    color: rgba(0,0,0,1);  
  }
  .app_navbar_links li:hover > ul {
    display: block;
    position: absolute;
  }
.app_links{
    position: relative;
    list-style: none;
    background-color: #fff;
    transition: 0.5s;
    
}
.iconSmall{
    font-size: 10px;
    margin-left: 5px;
   
    
}
.app_navbar_links .app_links2 .app_dropdown_menu .iconSmall{
    position: absolute;
    right: 5px;
    transform: rotate(270deg);
   
    
}

.app_dropdown_menu li{  
    border-bottom: 1px dashed #eee;   
   
}
.app_dropdown_menu a{
    
    color: rgba(0,0,0,.7);
    font-weight: 500;
}
 .app_dropdown_menu a:hover{ 
    color: rgba(0,0,0,1);  
}



.app_navbar_small{
  display: none;
}
.app_navbar_small_overlay{
    width: 100%;
    min-height: 80vh;
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
    background: #fff;
    transition: 0.5s ease;
    flex-direction: column;
    z-index: 10;

}

.app_navbar_small_overlay .overlay_close{
    font-size: 27px;
    position: absolute;
    top: 45px;
    right: 20px;
    color: #000;
    cursor: pointer;
    z-index: 20;
}
.app_navbarsmall_links{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  height: inherit;
  position: relative; 
 
}
.app_navbarsmall_links li{
    list-style: none;
    
    font-size: 22px;
    font-family: var(--font-base);
    padding-bottom: 0.5rem;
    
}
.app_navbarsmall_links ul {
    position: absolute;
    display: none;
   left: 100%;
   top: 0;
  }
  .app_navbarsmall_links .app_links2 ul .app_links2 ul{
    padding: 5px;
    display: none;
    /* box-shadow: 1px 1px 10px #6786c554; */
    left: 3%;
  }
  .app_navbarsmall_links .app_links2 ul .app_links2 ul li{
  
    border-bottom:  none;
   
  }
  
  .app_navbarsmall_links .app_links2 ul .app_links2:hover ul{
  display: block;
  
  } 
  .app_navbarsmall_links ul li a:hover{
    color: rgba(0,0,0,1);  
  }

  .app_navbarsmall_links li ul .iconSmall{
      position: absolute;
      right: 0;
     
      
  }
.tel{
    color: #3889bc;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 50px;
    position: relative;
    z-index: 100;
}
.app_navbarsmall_links li:hover{
    color: #3889bc;
   
}
.app_navbarsmall_links li:hover ul
 {
    display: flex;
    flex-direction: column;
    transition: 0.5s;
    
}

.social_icons{
    font-size: 20px;
    
 
}
.app_navbarsmall_links .app_dropdown_menu{
      position: relative;
      top: 10px;
      left: 3%;
      cursor: pointer;
      max-width: 280px;
      width: 100%;
      text-align: left;
}
.app_navbarsmall_links .app_dropdown_menu li::before{
    content: " - ";
    font-family: var(--font-alt);
}

.app_links_small:hover .app_dropdown_menu {
    display: block;
   
} 
.box_small{
    padding: 9rem 1rem;
}

@media(max-width: 992px){
    .app_navbar{
        padding: 1.5rem;
    }
    .app_navbar_links {
        display: none;
    }
    .app_navbar_small{
        display: flex;
        
    }
    .tel{
        margin-left: 0px;
    }
   
}
@media(max-width: 650px){
   
    .app_navbar_logo img{
        width: 100px;
    }
}