.app_aboutus{
    position: relative;
    margin-top: 50px;
}
.app_aboutus_overlay{   
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.app_aboutus_overlay img{
    max-width: 400px;
    width: 100%;
    z-index: 0;
    
}
.app_aboutus-content{
    width: 100%;
    z-index: 2;
    display: flex;
}
.app_aboutus-block{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    text-align: right;
}
.app_aboutus-block2{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: left;
}
.app_aboutus-text h3{
    color: #0b507e ;
    padding:  0.5rem 0;
    font-size: 25px;
    font-family: var(--font-base);
}
@media(max-width: 576px){
    .app_aboutus-block,.app_aboutus-block2{
       
        justify-content: center;
        align-items: center;
        text-align: center;
      
    }
 
    .app_aboutus-content{
         flex-direction: column;
         justify-content: center;
         align-items: center;
    }
    .app_aboutus_overlay{
        inset: 0;
    }
}