.price_block{
    margin: 30px 0px;
  
}

.price{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 30px;
      
}

.box{
    flex:32%;
    max-width: 400px;
    width: 100%;
    padding: 20px;
    background: rgba(255,255,255,.5);
    border-radius: 10px;
    margin: 10px;
    box-shadow: 1px 1px 3px rgba(0,0,0,.4), -1px -1px 3px rgba(0,0,0,.4);
    transition: 0.5s;
   
}
.box:hover{
    box-shadow: 1px 1px 7px rgba(0,0,0,.5), -1px -1px 7px rgba(0,0,0,.5);
}
.price_block h2{
    text-align: center;
    font-size: 25px;
    
}
.box h3{
    text-align: center;
    font-size: 20px;
    color: #0b507e;
}
.box p{
    font-size: 23px;
    text-align: center;
    margin: 20px 0;

}
.box_text{  
    height: 70px;
    
}
.box_text p{
  color: rgba(0,0,0,.7);
  font-size: 16px;
  margin: 5px 0;
}
.btn_box{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.box button{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 0;
    margin-top: 20px;
    margin-bottom: 30px;
    background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #5468ff 100%);
    box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;  
    color: #fff;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1; 
    overflow: hidden;
    transition: box-shadow .15s,transform .15s;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    will-change: box-shadow,transform;
 



}
.box button:focus {
    box-shadow: #3c4fe0 0 0 0 1.5px inset, rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
  }
  
  .box button:hover {
    box-shadow: rgba(45, 35, 66, .4) 0 4px 8px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
    transform: translateY(-2px);
  }
  
  .box button:active {
    box-shadow: #3c4fe0 0 3px 7px inset;
    transform: translateY(2px);
  }

.box .btn_icon{
    color: #fff;
    font-size: 20px;
}