.main_header{
    position: relative;
}
.header_img{
    width: 100%;
    height: 600px;
    position: relative;
}
.header_img img{
    width: 100%;
    height: 600px;
    object-fit: cover;
}
.headerBlack{
    width: 70%;
    height: inherit;
    background: rgba(0,0,0,.2);
    clip-path: polygon(11% 0, 86% 0, 68% 100%, 0% 100%);
    box-shadow: 1px 1px 5px rgba(0,0,0,.2);
    position: absolute;
    top:0;
    left: 0;
    z-index: 3;

}
.main_header .headerText{
    position: absolute;
    top:0;
    left: 0;
    width: 60%;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
}
.main_header .headerText h1{
    max-width: 60%;
    color: #fff;
}
.main_header .headerText p{
    padding: 1rem 0;
    color: rgb(202, 199, 199);
}
.app_icon{
    font-size: 18px;
}

@media(max-width: 992px){
    .main_header .headerText{       
        width: 90%;
        }
    .main_header .headerText h1{
        max-width: 80%;
        font-size: 30px;
    }
    .headerBlack {
        width: 90%;
        clip-path: polygon(3% 0, 93% 0, 79% 100%, 0% 100%)
    }
}

@media(max-width: 576px){
    .main_header .headerText{       
        width: 90%;
        height: 400px;}
    .main_header .headerText h1{
        max-width: 80%;
        font-size: 26px;
    }
    .header_img, .header_img img{
        height: 400px;      
    }
    .headerBlack{
        
        clip-path: polygon(0% 0, 93% 0, 79% 100%, 0% 100%);}
       
}