.bottomFooter {
    
    margin-top: 50px ;
    margin-bottom: 50px ;
    background: linear-gradient(#000000,#434343);
}
.foot_block{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 15px;
}
.foot_link{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 10px;
   
}
.foot_link a{
    
    color: rgba(255,255,255,.6);
    font-size: 15px;
    margin: 2px 13px;
    max-width: 270px;
    width: 100%;
    text-align: center;

}
.foot_link a:hover{
    color: rgba(255,255,255,1);
}
@media(max-width: 900px){
    .foot_link{
        justify-content: center;
    }
}
@media(max-width: 576px){
    .foot_block{
        justify-content: center;
    }
}