.bigbutton{
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 11
    

}
.bigbutton button{ 
  width: 100%;
  color: #fff;
  padding: 1rem;
  font-size: 27px;
  background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #5468ff 100%);
  box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;  
  cursor: pointer;
  border: 0;
  border-radius: 8px; 
  
}
.bigbutton_phone{
    font-size: 20px;
}