.footer_links{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 4rem 2rem;
    
}
.footer_contacts,
.footer_logo,
.footer_work{
    flex: 1;
    margin: 1rem;
    text-align: center;
}
.footer_logo img{
    width: 180px;
    margin-bottom: 2rem;
}
.footer_contacts h2,
.footer_work h2,
.footer_logo h2{
    font-size: 27px;
    color: #0b507e;
    font-family: var(--font-base);
    margin-bottom: 2rem;
}
.footer_contacts a,
.footer_work a,
.footer_logo p{
    font-size: 20px;
    color: rgba(0,0,0,.8);
    display: block;
    font-family: var(--font-base);
    padding: 3px;
    font-weight: 600;
}
.footer_work ul li{
    list-style: none;
}
.footer_work ul li a:hover{
    color: rgba(0,0,0,1);
}
.line{
    width: 100%;
    height: 1px;
    background-color: #eee;
    margin: 4rem 0 2rem;

}
.cont{
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.cont p, .cont a{
    margin: 5px;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.8);
}
@media(max-width: 700px){
    .footer_links{
        flex-direction: column;
        align-items: center;
        padding: 2rem 2rem 7rem;
    }
    .footer_logo img{
        width: 130px;
        
    }
    
}